.chatflow-list-dialog {
  width: 600px;
  max-height: 80vh;
  display: flex;
  flex-direction: column;

  h2[mat-dialog-title] {
    display: flex;
    align-items: center;
    gap: 12px;
    margin: 0;
    padding: 20px 24px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);

    mat-icon {
      font-size: 28px;
      width: 28px;
      height: 28px;
    }

    span {
      font-size: 20px;
      font-weight: 500;
    }
  }

  mat-dialog-content {
    padding: 0;
    overflow-y: auto;
    flex: 1;
    min-height: 400px;
    max-height: calc(80vh - 140px);
  }

  mat-dialog-actions {
    padding: 16px 24px;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    margin: 0;
    gap: 12px;

    button {
      mat-icon {
        margin-right: 4px;
      }
    }
  }
}

// 검색 바
.search-bar {
  padding: 16px 24px;
  background: rgba(255, 255, 255, 0.02);
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);

  .search-field {
    width: 100%;
    margin: 0;

    ::ng-deep .mat-mdc-form-field-flex {
      background: rgba(255, 255, 255, 0.05);
    }
  }
}

// 로딩 상태
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 24px;
  gap: 16px;

  p {
    margin: 0;
    color: rgba(255, 255, 255, 0.7);
  }
}

// 빈 상태
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 24px;
  text-align: center;

  mat-icon {
    font-size: 64px;
    width: 64px;
    height: 64px;
    color: rgba(255, 255, 255, 0.3);
    margin-bottom: 16px;
  }

  h3 {
    margin: 0 0 8px 0;
    font-size: 18px;
    font-weight: 500;
  }

  p {
    margin: 0;
    color: rgba(255, 255, 255, 0.6);
    font-size: 14px;
  }
}

// 챗플로우 목록
.chatflow-list {
  padding: 8px;
}

.chatflow-item {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 12px;
  margin-bottom: 8px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.03);
  border: 2px solid transparent;
  cursor: pointer;
  transition: all 0.2s ease;

  &:hover {
    background: rgba(255, 255, 255, 0.06);
    border-color: rgba(100, 181, 246, 0.3);
  }

  &.selected {
    background: rgba(100, 181, 246, 0.15);
    border-color: #64B5F6;

    .chatflow-name {
      color: #64B5F6;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.chatflow-thumbnail {
  flex-shrink: 0;
  width: 60px;
  height: 60px;
  border-radius: 6px;
  background: rgba(255, 255, 255, 0.05);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  mat-icon {
    font-size: 32px;
    width: 32px;
    height: 32px;
    color: rgba(255, 255, 255, 0.4);
  }
}

.chatflow-info {
  flex: 1;
  min-width: 0;
}

.chatflow-name {
  margin: 0 0 8px 0;
  font-size: 16px;
  font-weight: 500;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  transition: color 0.2s ease;
}

.chatflow-meta {
  display: flex;
  align-items: center;
  gap: 16px;
  font-size: 13px;
  color: rgba(255, 255, 255, 0.6);

  span {
    display: flex;
    align-items: center;
    gap: 4px;

    mat-icon {
      font-size: 16px;
      width: 16px;
      height: 16px;
    }
  }

  .node-count {
    color: rgba(100, 181, 246, 0.8);
  }
}

.chatflow-actions {
  display: flex;
  gap: 4px;
  flex-shrink: 0;

  button {
    opacity: 0;
    transition: opacity 0.2s ease;
  }

  .chatflow-item:hover &,
  .chatflow-item.selected & {
    button {
      opacity: 1;
    }
  }
}

// 통계 정보
.stats-info {
  padding: 12px 24px;
  background: rgba(255, 255, 255, 0.02);
  border-top: 1px solid rgba(255, 255, 255, 0.05);
  font-size: 13px;
  color: rgba(255, 255, 255, 0.6);
  text-align: center;

  span + span {
    margin-left: 8px;
  }
}

// 반응형
@media (max-width: 768px) {
  .chatflow-list-dialog {
    width: 100%;
    max-width: 100vw;
  }

  .chatflow-item {
    flex-wrap: wrap;

    .chatflow-actions {
      width: 100%;
      justify-content: flex-end;
      margin-top: 8px;

      button {
        opacity: 1;
      }
    }
  }
}
